    45deg,
    orange 0px,
    orange 10px,
    yellow 10px,
    yellow 20px,
    green 20px,
    green 30px,
    cyan 30px,
    cyan 40px,
    blue 40px,
    blue 50px,
    violet 50px,
    violet 60px
  );
}
#random {
  background-color: #eee;
  background-image: -webkit-linear-gradient(
      45deg,
      black 25%,
      transparent 25%,
      transparent 75%,
      black 75%,
      black
